:root {
    // light
    --primary-color: #3251df;
    --primary-color-darken: #1938c6;
    --secondary-color: #ebeefc;
    --contrast-color: #ffffff;

    // dark
    --dark-primary-color: #7b8fea;
    --dark-primary-color-darken: #6272bb;
    --dark-secondary-color: #2f4250;
    --dark-contrast-color: #ffffff;

    // common
    --camera-overlay-color: rgba(0, 0, 0, 0.5);
}
