@tailwind base;
@tailwind components;
@tailwind utilities;

@import 'variables';
@import 'coolicons';

@layer base {
    details > summary::-webkit-details-marker {
        @apply hidden;
    }
}

@layer components {
    .main-wrapper {
        @apply h-full sm:h-auto sm:w-[632px] sm:max-w-[632px] sm:py-8;
    }

    .main-container {
        @apply flex min-h-full w-screen flex-col bg-light-paper p-4 text-center dark:bg-dark-paper sm:mb-4 sm:h-fit sm:min-h-fit sm:w-auto sm:rounded sm:p-8 sm:shadow-lg;
    }

    .primary-button {
        @apply rounded bg-light-base-00 px-7 py-3 text-s15 
            text-light-base-contrast hover:bg-light-base-03 disabled:bg-light-gray-00 disabled:text-light-base-01 dark:bg-dark-base-00 dark:text-dark-base-contrast dark:hover:bg-dark-base-03 dark:disabled:bg-dark-gray-00 dark:disabled:text-dark-base-01;
    }

    .secondary-button {
        @apply rounded bg-light-action-00 px-7 py-3 text-s15 
            text-light-base-00 hover:bg-light-base-00 hover:text-light-base-contrast dark:bg-dark-action-00 dark:text-dark-base-00 dark:hover:bg-dark-base-00 dark:hover:text-dark-base-contrast;
    }

    .negative-feedback {
        @apply w-fit rounded bg-light-red-00 p-2 text-s13 text-light-paper;
    }

    .positive-feedback {
        @apply w-fit rounded bg-light-green-00 p-2 text-s13 text-light-paper;
    }

    #camera-preview {
        @apply -translate-z-30 sm:translate-z-10 absolute top-0 left-0 right-0 bottom-0 -z-30 h-full sm:relative sm:z-10;
        video {
            @apply h-full object-cover sm:rounded;
            -webkit-transform-style: preserve-3d; // https://stackoverflow.com/a/4895542
        }
    }

    .camera-preview-overlay {
        @apply -translate-z-20 absolute -z-20 h-full w-full after:absolute after:inset-x-[10vw] after:top-[calc((100%_-_calc(0.63_*_0.8_*_100vw))_*_0.5)] after:h-[calc(0.63_*_0.8_*_100vw)] after:rounded-md after:border-2 after:border-light-paper after:shadow-viewport;
    }

    .camera-preview-overlay-after {
        @apply absolute top-[calc(calc((100%_-_calc(0.63_*_0.8_*_100vw))_*_0.5)_+_calc(0.63_*_0.8_*_100vw))] w-full;
    }

    #snapshot-preview {
        @apply absolute inset-x-[10vw] top-[calc((100%_-_calc(0.63_*_0.8_*_100vw))_*_0.5)] h-[calc(0.63_*_0.8_*_100vw)] sm:static sm:h-auto;
        img {
            @apply relative top-0 left-0 h-[calc(0.63_*_0.8_*_100vw)] w-full rounded object-cover sm:h-auto sm:w-auto #{!important};
        }
    }

    #selfie-preview {
        @apply relative
        smo:top-0
        smo:left-0
        smo:right-0
        smo:flex
        smo:h-ellipseHeight
        smo:w-full
        sm:mb-8;

        img {
            @apply max-h-full
            object-cover
            smo:m-auto
            smo:w-[calc(var(--ellipseWidth)+2px)];
        }
    }

    #selfie-overlay {
        @apply absolute
        -top-[1px]
        left-1/2
        h-[calc(var(--ellipseHeight)+2px)]
        w-full
        -translate-x-1/2
        overflow-hidden
        after:absolute
        after:top-[1px]
        after:left-1/2
        after:h-ellipseHeight
        after:w-ellipseWidth
        after:-translate-x-1/2
        after:rounded-1/2
        after:shadow-viewport
        after:shadow-light-paper
        after:dark:shadow-dark-paper
        sm:h-[calc(100%+2px)]
        sm:after:top-1/2
        sm:after:-translate-x-1/2
        sm:after:-translate-y-1/2;
    }

    .welcome-slider {
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            @apply hidden;
        }
    }

    body > canvas {
        @apply -translate-z-10 sm:translate-z-10 -z-10 smo:left-0 smo:w-full sm:z-10 sm:w-auto sm:rounded #{!important};
    }
}

@layer utilities {
    // translate-z utilities - necesarry for z-index bug on older iOS
    .translate-z-10 {
        // -moz-transform: translateZ(10px);
        // -o-transform: translateZ(10px);
        -webkit-transform: translateZ(10px);
        transform: translateZ(10px);
    }

    .-translate-z {
        -webkit-transform: translateZ(-10px);
        transform: translateZ(-10px);
    }

    .-translate-z-20 {
        -webkit-transform: translateZ(-20px);
        transform: translateZ(-20px);
    }

    .-translate-z-30 {
        -webkit-transform: translateZ(-30px);
        transform: translateZ(-30px);
    }

    .dark {
        color-scheme: dark;
    }
}
